import React from 'react';
import styled from 'styled-components';
import {
  Modal,
  Input,
  Form
} from 'antd';
import {
  PlusOutlined
} from '@ant-design/icons';
import useToggle from '../../hooks/useToggle';
import PecsUpload from './PecsUpload';

const StyledPlusOutlined = styled(PlusOutlined)`
  color: white;
  font-size: 30px;
  margin-right: 10px;
  cursor: pointer;
`;

function AddPecs() {
  const [visible, toggleVisible] = useToggle(false);
  const [form] = Form.useForm();

  const handleOk = (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
    form.validateFields()
      .then(values => {
        form.resetFields();
        toggleVisible();
      });
  };

  const handleCancel = (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
    toggleVisible();
  };


  return (
    <>
      <StyledPlusOutlined onClick={toggleVisible} />
      <Modal
        title="Add Pecs"
        visible={visible}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <Form
          name="Add Pecs"
          form={form}
        >
          <Form.Item
            name="pecs"
            label="Pecs"
            valuePropName="pecs"
            rules={[
              {
                required: true,
                message: 'Please upload your pecs',
              },
            ]}
          >
            <PecsUpload />
          </Form.Item>
          <Form.Item
            name="name"
            label="Pecs Name"
            rules={[
              {
                required: true,
                message: 'Please input your pecs name',
              },
            ]}
          >
            <Input placeholder="Pecs Name" />
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
}

export default AddPecs;
