import React from 'react';
import {
  Modal,
  Input,
  Form
} from 'antd';
import {
  EditOutlined
} from '@ant-design/icons';
import useToggle from '../../hooks/useToggle';
import PecsUpload from './PecsUpload';

function AddPecs() {
  const [visible, toggleVisible] = useToggle(false);
  const [form] = Form.useForm();

  const handleOk = (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
    form.validateFields()
      .then(values => {
        form.resetFields();
        toggleVisible();
      });
  };

  const handleCancel = (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
    toggleVisible();
  };

  return (
    <>
      <EditOutlined onClick={toggleVisible} />
      <Modal
        title="Edit Pecs"
        visible={visible}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <Form
          name="Edit Pecs"
          form={form}
        >
          <Form.Item
            name="pecs"
            label="Pecs"
            valuePropName="pecs"
            rules={[
              {
                required: true,
                message: 'Please upload your pecs',
              },
            ]}
          >
            <PecsUpload />
          </Form.Item>
          <Form.Item
            name="name"
            label="Pecs Name"
            rules={[
              {
                required: true,
                message: 'Please input your pecs name',
              },
            ]}
          >
            <Input placeholder="Pecs Name" />
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
}

export default AddPecs;
