import React, { useState } from 'react';
import styled from 'styled-components';
import { AutoComplete } from 'antd';
import { fuzzyStringRank } from '../../utils';
import { pecsDict } from '../../assets/img/pecs';

const { Option } = AutoComplete;

// used to split pecs key and current input value to make option unique
const splitSymbol = '^';

const PecsWrapper = styled.img`
  height: 100px;
  width: 100px;
`;

const PecsPreview = styled.img`
  height: 40px;
  width: 40px;
  margin: 10px 0;
`;

const Description = styled.p`
  color: gray;
  font-weight: 500;
  font-size: 20px;
  padding-top: 10px;
`;

interface Props {
  value: string;
  pecs: Pecs | null;
  setPecs: React.Dispatch<React.SetStateAction<Pecs | null>>;
  setValue: React.Dispatch<React.SetStateAction<string>>;
}

const pecKeys = Object.keys(pecsDict);

// this element is outside the scope
// config this class in app.css
const dropdownClassName = 'auto-complete-dropdown';

function ToDoAutoComplete({
  value,
  pecs,
  setPecs,
  setValue
}: Props) {
  const [result, setResult] = useState<Pecs[]>([]);
  const handleSearch = (input: string) => {
    if (pecs) setPecs(null);

    // rank input value with pecs' keys by leven distance
    const rankedKeys = fuzzyStringRank(input, pecKeys);
    const rankedPecs = rankedKeys.map(k => {
      return {
        value: k + splitSymbol + input, // combined value -> "pec^input"
        key: k, // key of pecs
        src: (pecsDict as Dictionary<string>)[k] // src of pecs
      };
    });

    setResult(rankedPecs);
  };

  const handleSelect = (input: string) => {
    const [k, v] = input.split(splitSymbol);
    setPecs({
      value: v, // combined value -> "pec^input"
      key: k, // key of pecs
      src: (pecsDict as Dictionary<string>)[k] // src of pecs
    });
  };

  return (
    <>
      <AutoComplete
        onSearch={handleSearch}
        placeholder="e.g. Take a shower"
        onSelect={handleSelect}
        onChange={(input: string) => {
          // "pecs^input" -> parts[0] -> pecs
          // "pecs^input" -> parts[1] -> input
          const parts = input.split((splitSymbol));

          // exclude pecs from combined value
          setValue(parts[1] ? parts[1] : parts[0]);
        }}
        value={value}
        dropdownClassName={dropdownClassName}
      >
        {result.map((curPecs: Pecs) => (
          <Option key={curPecs.key} value={curPecs.value}>
            <PecsWrapper src={curPecs.src} />
            <Description>{curPecs.key}</Description>
          </Option>
        ))}
      </AutoComplete>
      {
        pecs
        &&
        <PecsPreview
          src={(pecsDict as Dictionary<string>)[pecs.key]}
        />
      }
    </>
  );
}

export default ToDoAutoComplete;
