/* eslint-disable no-console */
import leven from 'leven';

/**
 * Format Date
 * 
 * @param d Date object
 # @return 'mm/dd/yyyy'
 */
const formatDate = (d: Date): string => {
  let month = `${d.getMonth() + 1}`;
  let day = `${d.getDate()}`;
  const year = d.getFullYear();

  if (month.length < 2)
    month = `0${month}`;
  if (day.length < 2)
    day = `0${day}`;

  return [month, day, year].join('/');
};

/**
 * Rank String by leven distance
 * 
 * @param target: string
 * @param dict: string[]
 # @return string[]
 */
const fuzzyStringRank = (target: string, dict: string[]): string[] => {
  return dict.sort((s1, s2) => leven(s1, target) - leven(s2, target));
};

/**
 * Speak the string message
 * checkout - https://developers.google.com/web/updates/2014/01/Web-apps-that-talk-Introduction-to-the-Speech-Synthesis-API
 *
 * @param message: string
 # @return void
 */
const speak = (message: string): void => {
  // check if speech api is supported
  if (!SpeechSynthesisUtterance) {
    console.error('SpeechSynthesis api is not sported in your browser');
    return;
  }

  // init speech instance & voice
  const msg = new SpeechSynthesisUtterance(message);
  [msg.voice] = speechSynthesis.getVoices()
    .filter(voice =>
      voice.name === 'Google UK English Female'
    ); // voice loading is async, it may be empty and fallback to default voice

  // speak
  if (window && window.speechSynthesis) window.speechSynthesis.speak(msg);
};

export {
  formatDate,
  fuzzyStringRank,
  speak
};