import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Upload, message } from 'antd';
import { LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import { API } from '../../assets/const';

const Wrapper = styled.div`
  width: 400px;
  height: 400px;
  display: flex;
  justify-content: center;
`;

const StyledUpload = styled(Upload)`
  position: relative;
  width: 100%;
  height: 100%;
  .ant-upload {
    width: 100%;
    height: 100%;
    margin-bottom: 0px;
    img {
      width: 100%;
    }
  }
  .ant-upload.ant-upload-select-picture-card {
    border: none;
  }
  .ant-upload.ant-upload-select-picture-card > .ant-upload {
    padding: 0px;
  }
  .pecs-upload {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
`;

const UploadText = styled.a`
  position: absolute;
  color: white;
  z-index: 2;
  font-size: 20px;
  font-weight: 600;
  background: rgb(59,59,59);
  bottom: 0px;
  padding: 0 12px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: inline-block;
  :hover {
    color: white;
  }
  :focus {
    color: white;
  }
`;


function getBase64(img: Blob, callback: (imageUrl: string) => void) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result as string));
  reader.readAsDataURL(img);
}

function beforeUpload(file: RcFile) {
  const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
  if (!isJpgOrPng) {
    message.error('You can only upload JPG/PNG file!');
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error('Image must smaller than 2MB!');
  }
  return isJpgOrPng && isLt2M;
}

function PecsUpload({
  url = undefined,
  ...props
}: {
  url?: string;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
} & any) {
  const [loading, setLoading] = useState<boolean>(false);
  const [imgUrl, setImgUrl] = useState<string>();

  useEffect(() => {
    setImgUrl(url);
  }, [url]);

  const handleChange = (info: UploadChangeParam<UploadFile<unknown>>) => {
    if (!info.file.status) return;
    // if (info.file.status === 'uploading') {
    //   setLoading(true);
    //   return;
    // }
    // if (info.file.status === 'done') {
    // Get this url from response in real world.
    getBase64(info.file.originFileObj as Blob, (imageUrl: string) => {
      setImgUrl(imageUrl);
      setLoading(false);
    });
    // }
  };

  const uploadButton = (
    <div className='pecs-upload'>
      {loading ? <LoadingOutlined /> : <PlusOutlined />}
      <div> Upload</div>
    </div>
  );

  const renderUpload = () => {
    if (!loading && imgUrl)
      return (
        <>
          <img src={imgUrl} alt="pecs" style={{ width: '100%' }} />
          <UploadText> Upload </UploadText>
        </>
      );
    return uploadButton;
  };

  return (
    <StyledUpload
      {...props}
      name="pecs"
      action={`${API.BASE_API}pecs`}
      listType="picture-card"
      onChange={handleChange}
      showUploadList={false}
      beforeUpload={beforeUpload}
    >
      <Wrapper>
        {renderUpload()}
      </Wrapper>
    </StyledUpload>
  );
}

export default PecsUpload;
