export default class DemoProfileStore {
  /*
   * Profile Related Attributes and Functions
   */
  @observable
  clientProfile: ClientProfile | null = clientData;

  @observable
  selectedUserProfile: UserProfile | undefined | null
    = this.clientProfile?.users[0];

  @observable
  currentUser: UserProfile | undefined | null
    = null;

  @observable
  isLogin: boolean = true;

  @observable
  isLoading: boolean = false;

  @observable
  isAudioLoading: boolean = false;

  @observable
  isVideoLoading: boolean = false;

  @action.bound
  async checkSession() {
    return this.isLogin;
  }

  @action.bound
  async login(credentials: { client: string, password: string }) {
    this.clientProfile = clientData;
    this.isLogin = true;
    return true;
  }

  @action.bound
  async signUp(client: {client: string, password: string, iconId: number}) {
    return this.isLogin;
  }

  @action.bound
  logout() {
    this.clientProfile = null;
    this.isLogin = false;
  }

  @action.bound
  setCurrentUser(userID: number) {
    [this.currentUser] = (this.clientProfile as ClientProfile).users
      .filter(user => userID === user._id);
  }

  @action.bound
  updateClientName(client: string) {
    (this.clientProfile as ClientProfile).client = client;
  }

  @action.bound
  updateClientIcon(iconId: number) {
    (this.clientProfile as ClientProfile).iconId = iconId;
  }

  @action.bound
  async addUserProfile(userProfile: UserProfile) {
    this.setSelectedUserProfile(userProfile);
    (this.clientProfile as ClientProfile).users.unshift(userProfile);

    message.success({
      content: 'Profile has been added',
      style: {
        color: 'red',
      },
    });
  }

  @action.bound
  async updateUserProfile(userProfile: UserProfile) {
    const targetProfile = (this.clientProfile as ClientProfile).users.find(
      user => user._id === userProfile._id
    );

    if (targetProfile) {
      targetProfile.name = userProfile.name;
      targetProfile.dob = userProfile.dob;
      targetProfile.iconId = userProfile.iconId;
      targetProfile.fColor = userProfile.fColor;
      targetProfile.fVideo = userProfile.fVideo;
      targetProfile.fTemperature = userProfile.fTemperature;
      targetProfile.temperatureRange = userProfile.temperatureRange;
      targetProfile.timer = userProfile.timer;
      targetProfile.volume = userProfile.volume;
      targetProfile.medicalNotes = userProfile.medicalNotes;
    } else {
      message.error({
        content: 'Profile not exist',
        style: {
          color: 'black',
        },
      });
    }

    message.success({
      content: 'Profile has been updated',
      style: {
        color: 'black',
      },
    });
  }

  @action.bound
  async removeUserProfile(id: number) {
    (this.clientProfile as ClientProfile).users
      = (this.clientProfile as ClientProfile).users.filter(
        user => user._id !== id
      );
    this.setSelectedUserProfile(this.clientProfile?.users[0]);

    message.success({
      content: 'Profile has been removed',
      style: {
        color: 'black',
      },
    });
  }

  @action.bound
  setSelectedUserProfile(userProfile: UserProfile | null | undefined) {
    this.selectedUserProfile = userProfile;
  }

  /*
   * Audio/Video Related Attributes and Functions
   */
  @observable
  currentAudio: { id: number, cover: string } | undefined
    = this.clientProfile?.audioCollection;

  @observable
  currentVideo: { id: string } | undefined
    = this.clientProfile?.video;

  @observable
  audioCollections: AudioCollection[] = [];

  @observable
  videoResults: VideoResult[] = [];

  @action.bound
  async searchAudioCollection(name: string) {
    if (!name) return;
    this.isAudioLoading = true;

    const res = await fetch('/search/playlists_without_albums?' +
      `q=${name}` +
      `&client_id=${API.SOUND_CLOUD_CLIENT_ID}` +
      '&limit=20' +
      '&offset=0' +
      '&linked_partitioning=1' +
      `&app_version=${API.SOUND_CLOUD_APP_VERSION}` +
      '&app_locale=en');
    const data = await res.json();

    this.isAudioLoading = false;
    this.audioCollections = (data as AudioResponse).collection;
    if (this.videoResults === undefined || this.audioCollections.length === 0) {
      message.error({
        content: 'Track Not Found',
        style: {
          color: 'black',
        },
      });
    }
  }

  @action.bound
  async searchVideo(name: string) {
    if (!name) return;
    this.isVideoLoading = true;

    const res = await fetch(`${API.YOUTUBE_BASE_API}/search?` +
      `q=${name}` +
      `&key=${API.YOUTUBE_API_KEY}` +
      '&maxResults=20' +
      '&part=snippet' +
      '&type=video'
    );
    const data = await res.json();

    this.isVideoLoading = false;
    this.videoResults = (data as VideoResponse).items;
    if (this.videoResults === undefined || this.videoResults.length === 0) {
      message.error({
        content: 'Video Not Found',
        style: {
          color: 'black',
        },
      });
    }
  }

  @action.bound
  setVideo(id: string) {
    this.currentVideo = { id };
    (this.clientProfile as ClientProfile).video = { id };
    this.clearVideoResults();
  }

  @action.bound
  clearVideoResults() {
    this.videoResults = [];
  }

  @action.bound
  setAudioCollection(id: number, cover: string) {
    this.currentAudio = { id, cover };
    (this.clientProfile as ClientProfile).audioCollection
      = { id, cover };
    this.clearAudioCollection();
  }

  @action.bound
  clearAudioCollection() {
    this.audioCollections = [];
  }
}