import React, { useState, useContext } from 'react';
import { withTheme } from 'styled-components';
import { Form, Col, Row, Input, message } from 'antd';
import {
  UserOutlined,
  LockOutlined,
  CameraOutlined
} from '@ant-design/icons';
import { ProfileStoreCtx } from '../../../store/profileStore';
import AvatarPicker from '../../avatarPicker/avatarPicker';
import {
  FormWrapper,
  ConfirmBtn,
  CancelBtn,
  LoginBtn,
  FormDrawer
} from '../styled';

function SignUpForm({ theme }: { theme: AppTheme }) {
  const [visible, setVisible] = useState(false);
  const [form] = Form.useForm();
  const profileStore = useContext(ProfileStoreCtx);

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  const onSignUp = () => {
    form.validateFields()
      .then(async values => {
        form.resetFields();

        const success = await profileStore.signUp({
          client: values.client,
          password: values.password,
          iconId: values.iconId
        });

        if (!success) return;
        message.success('SignUp Success');
        setVisible(false);
      });
  };

  return (
    <FormWrapper>
      <LoginBtn onClick={showDrawer}> Sign Up</LoginBtn>
      <FormDrawer
        title="Create a new account"
        width={`calc(100% - ${theme.sider.width})`}
        onClose={onClose}
        visible={visible}
        bodyStyle={{ paddingBottom: 80 }}
        footer={
          <div>
            <CancelBtn onClick={onClose} style={{ marginRight: 8 }}>
              Cancel
            </CancelBtn>
            <ConfirmBtn onClick={onSignUp} type="primary">
              Sign Up
            </ConfirmBtn>
          </div>
        }
      >
        <Form
          layout="vertical"
          hideRequiredMark
          form={form}
        >
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item
                name="client"
                label={<span><UserOutlined /> Client Name</span>}
                rules={[{ required: true, message: 'Please enter client name' }]}
              >
                <Input placeholder="Please enter client name" />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={48}>
            <Col style={{ flex: 'unset', width: '100%', textAlign: 'left' }}>
              <Form.Item
                name="iconId"
                label={<span><CameraOutlined /> Choose Your Icon</span>}
                rules={[{ required: true, message: 'Please choose icon' }]}
              >
                <AvatarPicker />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item
                name="password"
                label={<span><LockOutlined /> Password</span>}
                rules={[{ required: true, message: 'Enter your password' }]}
              >
                <Input.Password placeholder="input password" />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item
                name="recheckPassword"
                label={<span><LockOutlined /> Confirm Password</span>}
                rules={[{ required: true, message: 'Confirm your password' }]}
              >
                <Input.Password placeholder="confirm password" />
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </FormDrawer>
    </FormWrapper>
  );
}

export default withTheme(SignUpForm);
