import React from 'react';
import styled from 'styled-components';
import { Card } from 'antd';
import {
  DeleteOutlined
} from '@ant-design/icons';
import { SectionHeader } from '../styled';
import AddPecs from './AddPecs';
import EditPecs from './EditPecs';
import { pecsDict } from '../../assets/img/pecs';

const { Meta } = Card;

const PecsManageContent = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

function PecsManagement() {
  return (
    <>
      <SectionHeader>
        <h1> Pecs Management </h1>
        <AddPecs />
      </SectionHeader>
      <PecsManageContent>
        {Object.keys(pecsDict).map(pecs =>
          <Card
            hoverable
            style={{
              width: 'calc(20% - 10px)',
              margin: '5px 5px 5px 5px'
            }}
            cover={
              <img
                alt="pecs"
                src={(pecsDict as Dictionary<string>)[pecs]}
              />
            }
            key={pecs}
            actions={[
              <EditPecs key="edit" />,
              <DeleteOutlined key="delete" />
            ]}
          >
            <Meta title={pecs} />
          </Card>
        )}
      </PecsManageContent>
    </>
  );
}

export default PecsManagement;
