/* eslint-disable no-underscore-dangle, @typescript-eslint/indent */
import { observable, action } from 'mobx';
import { createContext } from 'react';
import { message } from 'antd';
import { API } from '../assets/const';
import AuthService from '../services/Authentication';
// import ClientService from '../services/Client';
import UserService from '../services/User';
import DemoProfileStore from './profileStore.demo';


class ProfileStore {
  /*
   * Profile Related Attributes and Functions
   */
  @observable
  clientProfile: ClientProfile | null = null;

  @observable
  selectedUserProfile: UserProfile | undefined | null
    = this.clientProfile?.users[0];

  @observable
  isLogin: boolean = false;

  @observable
  currentUser: UserProfile | undefined | null
    = null;

  @observable
  isLoading: boolean = false;

  @observable
  isAudioLoading: boolean = false;

  @observable
  isVideoLoading: boolean = false;

  static async getUsersData(clientId: number):
    Promise<undefined | UserProfile[]> {
    const [err, usersData] = await UserService.getByClient(clientId);
    if (!usersData || err) {
      message.error({
        content: err ? err.error : 'Unexpected Error',
        style: {
          color: 'black',
        },
      });
      return undefined;
    }

    return usersData;
  }

  @action.bound
  async checkSession() {
    const [err, clientProfile] = await AuthService.checkSession();
    if (!clientProfile || err) {
      this.logout();
      return false;
    }

    this.currentAudio = clientProfile.audioCollection;
    this.currentVideo = clientProfile.video;

    const usersData
      = await ProfileStore.getUsersData(clientProfile._id);
    if (!usersData || usersData.length < 1) return true;

    clientProfile.users = usersData;
    this.setSelectedUserProfile(usersData[0]);
    this.clientProfile = clientProfile;

    this.isLogin = true;
    return true;
  }

  @action.bound
  async signUp(client: {client: string, password: string, iconId: number}) {
    const [err, clientData] = await AuthService.signUp(client);
    if (!clientData || err) {
      message.error({
        content: err ? err.error : 'Failed to signup',
        style: {
          color: 'black',
        },
      });
      return false;
    }
    this.isLogin = false; // just to use this (make it a non-static method)
    return true;
  }

  @action.bound
  async login(credentials: { client: string, password: string }) {
    // get client data
    const [err, clientData] = await AuthService.login(credentials);
    if (!clientData || err) {
      message.error({
        content: err ? err.error : 'Not Authorized',
        style: {
          color: 'black',
        },
      });
      return false;
    }

    // get sub users's data
    const usersData
      = await ProfileStore.getUsersData(clientData.client._id);

    if (!usersData) {
      this.clientProfile = clientData?.client ? clientData.client : null;
      this.isLogin = true;
      return true;
    }

    clientData.client.users = usersData;
    
    if (usersData.length > 0) this.setSelectedUserProfile(usersData[0]);

    this.clientProfile = clientData?.client ? clientData.client : null;
    this.isLogin = true;
    return true;
  }

  @action.bound
  logout() {
    AuthService.logout();
    this.clientProfile = null;
    this.isLogin = false;
  }

  @action.bound
  setCurrentUser(userID: number) {
    [this.currentUser] = (this.clientProfile as ClientProfile).users
      .filter(user => userID === user._id);
  }

  @action.bound
  updateClientName(client: string) {
    (this.clientProfile as ClientProfile).client = client;
  }
  
  @action.bound
  updateClientIcon(iconId: number) {
    (this.clientProfile as ClientProfile).iconId = iconId;
  }

  @action.bound
  async addUserProfile(userProfile: UserProfile) {
    const [err, addedUserProfile] = await UserService.add(userProfile);
    if (!addedUserProfile || err) {
      if (err?.code === 401) this.logout();
      message.error({
        content: err ? err.error : 'Unexpected Error',
        style: {
          color: 'black',
        },
      });
    } else {
      this.setSelectedUserProfile(addedUserProfile);
      (this.clientProfile as ClientProfile).users.unshift(addedUserProfile);

      message.success({
        content: 'Profile has been added',
        style: {
          color: 'red',
        },
      });
    }
  }

  @action.bound
  async updateUserProfile(userProfile: UserProfile) {
    const [err, updatedUserProfile] = await UserService.update(userProfile);
    if (!updatedUserProfile || err) {
      if (err?.code === 401) this.logout();
      message.error({
        content: err ? err.error : 'Unexpected Error',
        style: {
          color: 'black',
        },
      });
    } else {
      const targetProfile = (this.clientProfile as ClientProfile).users.find(
        user => user._id === userProfile._id
      );

      if (targetProfile) {
        targetProfile.name = userProfile.name;
        targetProfile.dob = userProfile.dob;
        targetProfile.iconId = userProfile.iconId;
        targetProfile.fColor = userProfile.fColor;
        targetProfile.fVideo = userProfile.fVideo;
        targetProfile.fTemperature = userProfile.fTemperature;
        targetProfile.temperatureRange = userProfile.temperatureRange;
        targetProfile.timer = userProfile.timer;
        targetProfile.volume = userProfile.volume;
        targetProfile.medicalNotes = userProfile.medicalNotes;
      } else {
        message.error({
          content: 'Profile not exist',
          style: {
            color: 'black',
          },
        });
      }

      message.success({
        content: 'Profile has been updated',
        style: {
          color: 'black',
        },
      });
    }
  }

  @action.bound
  async removeUserProfile(id: number) {
    const [err, deletedUserProfile] = await UserService.delete(id);
    if (!deletedUserProfile || err) {
      if (err?.code === 401) this.logout();
      message.error({
        content: err ? err.error : 'Unexpected Error',
        style: {
          color: 'black',
        },
      });
    } else {
      (this.clientProfile as ClientProfile).users
        = (this.clientProfile as ClientProfile).users.filter(
          user => user._id !== id
        );
      this.setSelectedUserProfile(this.clientProfile?.users[0]);

      message.success({
        content: 'Profile has been removed',
        style: {
          color: 'black',
        },
      });
    }
  }

  @action.bound
  setSelectedUserProfile(userProfile: UserProfile | null | undefined) {
    this.selectedUserProfile = userProfile;
  }

  /*
   * Audio/Video Related Attributes and Functions
   */
  @observable
  currentAudio: { id: number, cover: string } | undefined
    = this.clientProfile?.audioCollection;

  @observable
  currentVideo: { id: string } | undefined
    = this.clientProfile?.video;

  @observable
  audioCollections: AudioCollection[] = [];

  @observable
  videoResults: VideoResult[] = [];

  @action.bound
  async searchAudioCollection(name: string) {
    if (!name) return;
    this.isAudioLoading = true;

    const res = await fetch('/search/playlists_without_albums?' +
      `q=${name}` +
      `&client_id=${API.SOUND_CLOUD_CLIENT_ID}` +
      '&limit=20' +
      '&offset=0' +
      '&linked_partitioning=1' +
      `&app_version=${API.SOUND_CLOUD_APP_VERSION}` +
      '&app_locale=en');
    const data = await res.json();

    this.isAudioLoading = false;
    this.audioCollections = (data as AudioResponse).collection;
    if (this.videoResults === undefined || this.audioCollections.length === 0) {
      message.error({
        content: 'Track Not Found',
        style: {
          color: 'black',
        },
      });
    }
  }

  @action.bound
  async searchVideo(name: string) {
    if (!name) return;
    this.isVideoLoading = true;

    const res = await fetch(`${API.YOUTUBE_BASE_API}/search?` +
      `q=${name}` +
      `&key=${API.YOUTUBE_API_KEY}` +
      '&maxResults=20' +
      '&part=snippet' +
      '&type=video'
    );
    const data = await res.json();

    this.isVideoLoading = false;
    this.videoResults = (data as VideoResponse).items;
    if (this.videoResults === undefined || this.videoResults.length === 0) {
      message.error({
        content: 'Video Not Found',
        style: {
          color: 'black',
        },
      });
    }
  }

  @action.bound
  setVideo(id: string) {
    this.currentVideo = { id };
    (this.clientProfile as ClientProfile).video = { id };
    this.clearVideoResults();
  }

  @action.bound
  clearVideoResults() {
    this.videoResults = [];
  }

  @action.bound
  setAudioCollection(id: number, cover: string) {
    this.currentAudio = { id, cover };
    (this.clientProfile as ClientProfile).audioCollection
      = { id, cover };
    this.clearAudioCollection();
  }

  @action.bound
  clearAudioCollection() {
    this.audioCollections = [];
  }
}

export const ProfileStoreCtx =
  process.env.NODE_ENV === 'test'
    ? createContext(new ProfileStore())
    : createContext(new DemoProfileStore() as unknown as ProfileStore);

